@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{box-sizing:border-box}body{margin:0px;font-family:'Roboto', sans-serif}h1,h2,h3,h4,h5,h6{margin:0px}main{padding:1rem}.App{min-height:100vh}.dark{background-color:#202020 !important;color:#fafafa !important}.dark input,.dark label,.dark button{color:#fafafa !important}.light{background-color:#fff;color:#202020}.App-header{display:flex;background-color:rgba(63,81,181,0.8)}.logo-container{flex-grow:1;display:flex;align-items:center}.logo-container img{padding-left:0.5rem;height:2rem}.navbar{display:none}.switch-off-state{margin-right:20px}@media screen and (min-width: 48rem){.navbar{display:flex;width:12rem;justify-content:space-evenly;align-items:center}.navbar .navlink{line-height:3rem;color:rgba(250,250,250,0.8);text-decoration:none}.navbar .navlink:hover{color:#fafafa}.navbar .navlink-active{color:#fafafa}}.search-container{display:flex;justify-content:center}.weather-container{min-height:80vh;display:flex;flex-direction:column;justify-content:space-between}.weather-top{color:#eee;display:flex;justify-content:space-between}.weather-main{color:#e4e4e4;display:flex;justify-content:center;align-items:center}.weather-main h2{text-shadow:0px 0px 10px rgba(32,32,32,0.3)}.weather-forecast{display:flex;justify-content:center;flex-wrap:wrap;padding-bottom:2rem}.daily-forecast-day{position:relative;border:1px solid #202020;border-radius:0.25rem;padding:1rem;margin:0.5rem;display:flex;flex-direction:column;align-items:center}.daily-forecast-day *{margin-top:0.375rem}.daily-forecast-date,.daily-forecast-temp,.daily-forecast-icon{position:relative}.daily-forecast-bg{position:absolute;border-radius:3px;top:-6px;z-index:0;width:100%;height:100%;opacity:0.87}.night-time{background:linear-gradient(0deg, #70b144 0%, #70b144 16%, #01426d 20%, #001d37 100%)}.night-time .weather-top svg{color:white}.day-time{background:linear-gradient(0deg, #70b144 0%, #70b144 16%, #9fe9fc 20%, #009ffc 100%)}.bottom-navigation{position:fixed;width:100%;bottom:0px}@media screen and (min-width: 48rem){.bottom-navigation{display:none}}.favorites-section{display:grid;grid-template-columns:repeat(auto-fit, minmax(10rem, 1fr));grid-gap:0.5rem;gap:0.5rem}.unfavorite-container{position:absolute;right:0rem;top:0rem;width:3rem;height:3rem;display:none}.unfavorite-container:hover{color:rgba(250,250,250,0.7)}.mini-city-container{cursor:pointer;position:relative;border:1px solid #202020;color:white;border-radius:0.25rem;padding:1rem;text-align:center;display:flex;flex-direction:column;align-items:center}.mini-city-container:hover .unfavorite-container{display:block}.mini-city-container *{margin-top:0.375rem}.mini-city-container.dark{border:1px solid #fafafa;background-color:rgba(250,250,250,0.18) !important}.favorites-empty-state h3{margin-top:0.75rem;text-decoration:underline;cursor:pointer}.favorites-empty-state h3:hover{opacity:0.9}.favorites-empty-state img{max-height:40rem;width:100%}

